#chart {
    position: relative;
  }
  
  .controls {
    margin-bottom: 20px;
  }
  
  .radio-buttons {
    margin-bottom: 10px;
  }
  
  label {
    margin-right: 15px;
  }
  
  select {
    margin-left: 10px;
  }
  .tooltip {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.495);
    color: white;
    font-size: 12px;
    padding: 8px;
    border-radius: 5px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2); 
    pointer-events: none; 
    visibility: hidden; 
    z-index: 10; 
    transition: all 0.2s ease-in-out; 
  }
  
  